import React from "react";

interface Business {
  _id: string;
  name: string;
  category: string;
  description: string;
  logo?: { url: string };
  location: {
    address: string;
    state?: string;
    country: string;
    zipCode: string;
  };
  contactInfo: {
    phone: string;
    email: string;
    website?: string;
  };
  owner: {
    username: string;
  };
}

async function getBusiness(id: string): Promise<Business | null> {
  try {
    const res = await fetch(`http://localhost:5000/api/business/viewBiz/${id}`, {
      cache: "no-store",
    });
    const data = await res.json();
    return data.Biz;
  } catch (error) {
    console.error("Fetch error:", error);
    return null;
  }
}

export default async function Business({ params }: { params: { id: string } }) {
  const business = await getBusiness(params.id);

  if (!business) {
    return <div className="p-6 text-red-600">Business not found.</div>;
  }

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-6">
      <div className="flex items-center gap-4">
        <img
          src={business.logo?.url || "/default-logo.png"}
          alt="Logo"
          className="w-24 h-24 rounded-full object-cover border border-black"
        />
        <div>
          <h1 className="text-3xl font-bold">{business.name}</h1>
          <p className="text-sm text-gray-600">{business.category}</p>
        </div>
      </div>

      <div>
        <h2 className="text-xl font-semibold">Description</h2>
        <p className="text-gray-700">{business.description}</p>
      </div>

      <div>
        <h2 className="text-xl font-semibold">Location</h2>
        <p>
          {business.location.address}, {business.location.state && `${business.location.state}, `}
          {business.location.country} - {business.location.zipCode}
        </p>
      </div>

      <div>
        <h2 className="text-xl font-semibold">Contact Info</h2>
        <p>üìû {business.contactInfo.phone}</p>
        <p>üìß {business.contactInfo.email}</p>
        {business.contactInfo.website && (
          <p>üåê <a href={business.contactInfo.website} className="text-blue-600 underline" target="_blank">{business.contactInfo.website}</a></p>
        )}
      </div>

      <div>
        <h2 className="text-xl font-semibold">Owner</h2>
        <p>{business.owner.username}</p>
      </div>
    </div>
  );
}
